.radio_old {
    position: absolute;
    display: none;
}



.radio_custom {
    display: flex;
    align-items: center;
}


.radio_circle {
    width: 20px;
    height: 20px;
    top: 0;
    border: solid 2px #6534FF;
    border-radius: 50%;
    box-sizing: border-box;
    transition: .3s;
}

.radio_circle:hover {
    transform: scale(1.05);
}




.radio_old:checked+label .radio_circle {
    border-width: 6px;
}

.radio-text {
    padding-left: 14px;
    font-size: 16px;
    font-weight: 500;
}