.loader {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.user-plug {
    display: flex;
    gap: 16px;
    align-items: center;
    margin: 16px;
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 16px;
    box-sizing: border-box;
    

}

.circle {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    background: linear-gradient(90deg, #F3F3F6 0%, #FAFAFA 100%);
    background-position: 0 0;
    animation: background 2s infinite alternate;
}

.lines-wrapper {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.lines-wrapper div {
    background: linear-gradient(90deg, #F3F3F6 0%, #FAFAFA 100%);
    animation: background 2s infinite alternate;
    border-radius: 50px;
}

.line-big {
    width: 144px;
    height: 16px;
}

.line-small {
    width: 80px;
    height: 12px;
}

@keyframes background {
    100% {
        background-position: 0 200px;
    }
}